.user-navigation {
  @include list-reset();

  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-end;
  width: 235px;
  padding-top: 12px;

  font-size: 14px;
}

.user-navigation__item {
  margin-left: 30px;
  margin-bottom: 5px;
}

.user-navigation__link {
  padding: 3px;

  font-size: 14px;
  color: inherit;

  transition: opacity 0.3s;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.user-navigation__link-img {
  display: none;
}

@media (max-width: $tablet-width) {
  .user-navigation {
    width: 25px;
    padding-top: 24px;
  }

  .user-navigation__item--burger-menu {
    display: none;
  }

  .user-navigation__item--img-cart span,
  .user-navigation__item--img-cart .user-navigation__link {
    display: none;
  }

  .user-navigation__link-img {
    display: block;
  }
}

@media (max-width: $mobile-width) {
  .user-navigation {
    padding-top: 17px;
  }
}
